@import url('https://fonts.googleapis.com/css?family=Poiret+One');
@import url('https://fonts.googleapis.com/css?family=Orbitron');
@import url('https://fonts.googleapis.com/css?family=Roboto');
@import './mixins/box-shadow';
@import './partials/main.scss';
@import './partials/assign.scss';

$element-bg-color: rgba(24, 24, 24, 0.7);

html,
body,
main {
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%;
}

ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  // background-image: url('https://media.giphy.com/media/3o6vXTpomeZEyxufGU/giphy.gif');
  background-image: url('https://i.imgur.com/2m5xkfp.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 960px;
  padding: 0 20%;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
}

.header {
  @include box-shadow(1);

  background-color: $element-bg-color;
  display: inline-block;
  height: 4em;
  padding: px;
  top: 4em;
  width: 100%;

  &__navigation {
    font-family: 'Orbitron';
    color: #ccc;
    font-size: 26px;
    height: 100%;
  }

  &__list {
    list-style-type: none;
    height: 100%;
    list-style: none;
    display: flex;

    &-item {
      display: inline-block;
      width: 33%;
      height: 100%;
      text-align: center;
      flex: 1;
    }
  }

  &__link {
    position: relative;
    display: inline-block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;

    border: 0.5px solid #dfe1e5;
    border-top: none;
    border-bottom: none;

    & span {
      display: inline-block;
      padding-top: 18px;
    }

    &:hover,
    &[selected] {
      background-color: darken($element-bg-color, 0.3);
    }

    &:after {
      display: none;
    }

    &:hover:after,
    &[selected]:after {
      -webkit-animation: border-expand 0.2s cubic-bezier(0.4, 0, 0.4, 1) 0.01s alternate forwards;
      //transition: all 1s cubic-bezier(.4, 0, 1, 1);
      animation: border-expand 0.2s cubic-bezier(0.4, 0, 0.4, 1) 0.01s alternate forwards;
      background: #fff;
      bottom: 0;
      content: ' ';
      display: inline-block;
      height: 4px;
      position: absolute;
      right: 0;
      transition: 3s;
      width: 100%;
    }
  }
}
