.main {
  height: 100%;
}

.panel {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 960px;
  padding: 0 20%;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  background-color: rgba(24, 24, 24, 0.7);
  display: flex;

  border-radius: 5px 5px 0 0;

  &-container {
    position: absolute;
    height: 75px;
    width: 100%;
    bottom: 0;
  }

  &__item-container {
    position: relative;
    width: 33.33%;
    flex: 1;
  }

  &__image {
    position: relative;
    display: block;
    margin: 0 auto;
    padding-bottom: 20px;
    top: -32px;
    &-container {
      padding: 0 20%;
      padding-left: 10px;
      padding-right: 10px;
      width: 100%;
    }
  }
}
.arrow-container {
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: rgba(24, 24, 24, 0.7);
  height: 100%;
  font-size: 26px;
  font-family: 'Orbitron';
  padding: 0 15px;
  border-radius: 3px 0 0 0;

  &:hover {
    background-color: darken(rgba(24, 24, 24, 0.7), 0.3);
  }

  a {
    display: block;
    text-decoration: none;
    font-style: none;
    color: #fff;
    width: 100%;
    height: 100%;
    padding-top: 20px;
  }

  img {
    position: relative;
    top: -75px;
    width: 120px;
    height: 120px;
    fill: red;
  }
}
