.assignment-list {
  &__container {
    width: 80%;
    margin: 15px auto 0 auto;
    background-color: rgba(24, 24, 24, 0.7);
    border-radius: 3px 3px 3px 3px;
    height: 80%;
    padding-top: 20px;
    display: flex;
    padding: 15px;
  }
  &__item {
    list-style: none;
    flex: 1;
  }
  &__link {
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 400;
    text-decoration: none;
    color: #ccc;
    &:hover {
      text-decoration: underline;
    }
  }
}
